/**
 * @module  fixtip
 * @description  移动端toaster提示信息
 * @author   宇尘
 * @version   0.1.0
 * @param {Object} options - 参数
 * @param {string} options.msg - 提示消息
 * @param {string} options.timer - 选填 显示时间 默认3000毫秒
 * @param {string} options.bottom - 选填 距离底部的位置 默认为10%
 * @param {function} options.callback - 选填 消失时回调
 * @example  
 *  new Fixtip({
 * 		msg: '格式错误',
 * 		timer: 3000,
 * 		bottom: '100px',
 *		zIndex: 1000,
 * 		callback:function(){}
 * 	})
 * @return {function} Fixtip构造函数
 */
;(function(){

	require('./css/fixtips.css');

	function Fixtip(options) {
		this.options = options;

		this._init();
		this._render();
	}

	Fixtip.prototype._init = function() {
		var defaults = {
			timer: 3000,
			bottom: '10%',
			callback: null,
			zIndex: 1000
		}

		this._config = $.extend({}, defaults, this.options);
	}

	Fixtip.prototype._render = function() {
		var config = this._config;
		var $fixtip = null;

		if ($('.ui-fixtip').length == 0) {
			$fixtip = $('<div class="ui-fixtip"><span>'+
				config.msg+'</span></div>').appendTo('body');

			$fixtip.css({
				bottom: config.bottom,
				'z-index': config.zIndex
			});

			setTimeout(function() {
				$fixtip.remove();
				if(typeof config.callback == "function"){
					config.callback();
				}
			}, config.timer);
		}
		
	}

 	if (typeof module !== 'undefined' && typeof exports === 'object') {
        module.exports = Fixtip;
    } else if (typeof define === 'function' && (define.amd || define.cmd)) {
        define(function() { return Fixtip; });
    } else {
        this.Fixtip = Fixtip;
    }
}).call(function() {
    return this || (typeof window !== 'undefined' ? window : global);
});


