/**
 * login
 * version 3.2
 */
const getAppInfo = require('./getAppInfo');

module.exports = function login(){
    return new Promise( (resolve,reject) => {
        let bridge = window.bridge;
        bridge.callHandler('login',{}, (ret) =>{
            getAppInfo().then( info => {
                resolve(info);
            });
        })
    })
}



