/**
 * 判断用户是否登录
 * @return {Boolean}
 */
module.exports = function isLogin(){
    let bridge = window.bridge;
    if(!bridge.appInfo){
        return false;
    }
    var userId = parseInt(bridge.appInfo.userId);
    if(userId){
        return true;
    }
    return false;
}


