module.exports = function getAppInfo() {
    return new Promise((resolve, reject) => {
        let bridge = window.bridge;
        bridge.callHandler("getAppInfo",{}, ret => {
            let appInfo = bridge.toJson(ret);
            bridge.appInfo = appInfo;
            resolve( appInfo ) ;
        });
    })
}


